const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: {
				team_relate_1: '',
				team_relate_2: '',
				post_type: 'post',
				token: ''
			}
		}
	},
	onLoad() {
		this.token = uni.getStorageSync('token')
		this.getList()
	},
	methods: {

		getList() {
			uni.request({
				url: `${helper.requestUrl}/promote-setting`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					this.list.team_relate_1 = res.data.team_relate_1
					this.list.team_relate_2 = res.data.team_relate_2
					console.log(res.data, '**');
				}
			})
		},
		getSetData(e) {
			console.log(e)
			let formNode = this.list
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.list = formNode
		},
		determine() {
			let add = parseFloat(this.list.team_relate_1) + parseFloat(this.list.team_relate_2)
			console.log(add)
			if (this.list.team_relate_1 == '') {
				uni.showToast({
					title: '请输入奖励比例',
					icon: 'none'
				})
				return false
			}
			if (!(/(^[0-9]\d*$)/.test(this.list.team_relate_1))) {
				uni.showToast({
					title: '请输入整数',
					icon: 'none'
				})
				return false
			}
			// if (this.list.team_relate_1 + this.list.team_relate_2) {
			// 	uni.showToast({
			// 		title: '请输入奖励比例0-100',
			// 		icon: 'none'
			// 	})
			// 	return false
			// }
			if (this.list.team_relate_2 == '') {
				uni.showToast({
					title: '请输入奖励比例',
					icon: 'none'
				})
				return false
			}
			if (!(/(^[0-9]\d*$)/.test(this.list.team_relate_2))) {
				uni.showToast({
					title: '请输入整数',
					icon: 'none'
				})
				return false
			}
			if (add != 100) {
				uni.showToast({
					title: '请输入奖励比例100',
					icon: 'none'
				})
				return false
			}
			uni.request({
				url: `${helper.requestUrl}/promote-setting`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: this.list,
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					uni.showToast({
						title: res.data.tip_msg,
						icon: "none"
					});
					setTimeout( e =>{
						uni.navigateBack({
							delta: 1
						})
					},2000)
				}
			})
		}
	}
}
